'use client';

import { useState } from 'react';


export default function SignUp() {
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');

  const handleSubmit = (e) => {
    e.preventDefault();
    console.log('Signup →', { email, password });
    // → your auth logic here
  };

  return (
    <LayoutWrapper>
      {/* Full-screen split */}
      <div className="min-h-screen flex flex-col md:flex-row">

        {/* ───── LEFT – FORM ───── */}
        <div className="flex-1 flex items-center justify-center bg-gradient-to-br from-[#f5f5f5] to-[#e0e0e0] p-6 md:p-12">
          <div className="w-full max-w-sm">

            {/* Logo */}
            <Link href="/" className="inline-flex items-center gap-2 mb-10">
              <div className="w-9 h-9 bg-green-700 rounded-lg flex items-center justify-center">
                <span className="text-white font-black text-lg">P</span>
              </div>
              <span className="text-2xl font-bold text-gray-800">PayFuture</span>
            </Link>

            {/* Welcome text */}
            <h2 className="text-xl font-medium text-gray-700 mb-2">
              Welcome to <span className="font-bold">PayFuture</span> – Let’s create your account
            </h2>

            {/* Form */}
            <form onSubmit={handleSubmit} className="space-y-5">
              <div>
                <label className="sr-only" htmlFor="email">Email</label>
                <input
                  id="email"
                  type="email"
                  value={email}
                  onChange={e => setEmail(e.target.value)}
                  placeholder="you@example.com"
                  required
                  className="w-full px-4 py-3 text-gray-800 bg-white border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-green-600"
                />
              </div>

              <div>
                <label className="sr-only" htmlFor="password">Password</label>
                <input
                  id="password"
                  type="password"
                  value={password}
                  onChange={e => setPassword(e.target.value)}
                  placeholder="••••••••"
                  required
                  className="w-full px-4 py-3 text-gray-800 bg-white border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-green-600"
                />
              </div>

              <button
                type="submit"
                className="w-full py-3.5 font-semibold text-white bg-green-700 rounded-lg hover:bg-green-800 transition-colors"
              >
                Sign up
              </button>
            </form>

            {/* Footer links */}
            <p className="mt-6 text-center text-sm text-gray-600">
              Already have an account?{' '}
              <Link href="/signin" className="font-medium text-green-700 hover:underline">
                Log in
              </Link>
            </p>
          </div>
        </div>

        {/* ───── RIGHT – HERO ───── */}
        <div className="flex-1 relative bg-gradient-to-br from-green-900 to-green-700 flex items-center justify-center overflow-hidden">

          {/* Tagline */}
          <div className="absolute inset-0 flex flex-col items-center justify-center text-center px-8 text-white">
            <h1 className="text-4xl md:text-5xl lg:text-6xl font-bold leading-tight">
              Enter the Future<br />of Payments,<br />today
            </h1>
          </div>

          {/* Floating card preview */}
          <div className="absolute right-8 top-1/2 -translate-y-1/2 w-80 md:w-96 bg-white rounded-2xl shadow-2xl p-6 flex flex-col gap-4 transform rotate-3 hover:rotate-0 transition-transform">
            {/* Logo */}
            <div className="flex items-center gap-2">
              <div className="w-8 h-8 bg-green-700 rounded-lg flex items-center justify-center">
                <span className="text-white font-black text-sm">P</span>
              </div>
              <span className="font-semibold text-gray-800">PayFuture</span>
            </div>

            {/* Balance */}
            <div className="text-right">
              <p className="text-3xl font-bold text-gray-800">$12,347.23</p>
            </div>

            {/* Card number */}
            <div className="flex items-center justify-between text-sm text-gray-600">
              <span>•••• •••• •••• 2545</span>
              <span className="flex items-center gap-1">
                <svg className="w-5 h-5" viewBox="0 0 24 24" fill="currentColor">
                  <path d="M12 2C6.48 2 2 6.48 2 12s4.48 10 10 10 10-4.48 10-10S17.52 2 12 2zm-2 15l-5-5 1.41-1.41L10 14.17l7.59-7.59L19 8l-9 9z"/>
                </svg>
                VISA
              </span>
            </div>

            {/* Expiry */}
            <div className="text-right text-xs text-gray-500">
              Valid Thru 12/28
            </div>
          </div>
        </div>
      </div>
    </LayoutWrapper>
  );
}